:root {
  --color: #00acdc;
  --link-height: 60px;
}

html {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:last-child {
  margin: 0;
}

.logo {
  cursor: pointer;
  height: 40px;
  margin: 0 5px 0 0;
}

.nav__link {
  line-height: var(--link-height);
  height: var(--link-height);
  text-wrap: nowrap;
  cursor: pointer;
  display: inline-block;
}

.is-active {
  color: var(--color);
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  width: auto;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: var(--color);
}

